

/*Generales*/

*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body{
  font-family: verdana;
  width: 80%;
  margin: 0 auto;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
/*Header*/
.logo{
  text-align: center;
  font-size: 1.8em;
}
.contenedor{
  text-align: center;
}
.contenedor #fecha{
  float: left;
  margin-top:35px;
  display: inline-block;
}
.contenedor #nombre{
  float: right;
  display: inline-block;
  margin-top: 0px;

  color: #218DEF;
}
.contenedor .nav-principal{

  width: 25%;


  display: inline-block;
  margin-right:100px;
}
.contenedor .nav-principal nav {
  text-align: center;


}
.contenedor .nav-principal nav ul{

  margin-right: 40px;
}

.contenedor .nav-principal li{

  display: inline-block;
  margin: 10px 20px;

}
.pres{
  height: 500px;
  position: relative;
  z-index: 0;
}
.pres #contenedor{
  height: 500px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.pres #gradiente{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 500px;
  -webkit-filter: opacity(70%);
          filter: opacity(70%);
  background: -webkit-gradient(linear, left top, right top, from(#441178),color-stop(50%, #2989d8),color-stop(51%, #207cca),to(#1177cc));
  background: -webkit-linear-gradient(left, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);
  background: -o-linear-gradient(left, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);
  background: linear-gradient(to right, #441178 0%,#2989d8 50%,#207cca 51%,#1177cc 100%);

  z-index: 1;
}

.pres .im{
  background-repeat: no-repeat;
  background-repeat: round;
  height: 500px;
  width:50%;
  float: left;




}
.pres #gradiente #texto-pres{
  color: 	rgb(255,255,255,1);
  float: left;
  width: 33%;
  -webkit-filter: opacity(100%)!important;
          filter: opacity(100%)!important;
  padding:100px;
  font-size: 1.5em;
  font-weight: bold;

}

.pres #autor{
  font-size: 0.8em;
}
.main #Titulares h2{
  float: left;
}
.main #Titulares p{
  float: right;
  font-weight: bold;
}
.main .main-cuerpo{
  width: 70%;
  float:left;
}
.main .aside{
  width: 30%;
  float:right;
}
.main .main-cuerpo .categoria{
  width: 45%;
  float:left;
  text-align: left;
  padding: 20px;
}
.main .main-cuerpo .categoria .imagen{
  width:100%;
  height: 200px;

  margin-bottom: 70px;
}

.main .main-cuerpo .categoria .imagen img{
  width:100%;
}
.main .aside #top-noticias{
  padding: 10px;
  text-align: left;
  border: 1px solid black;
}
.main .aside .noticiaTop{
  margin-bottom: 80px;
}
.main .aside .imagen{
  float: left;
  width:25%;
  height: 90px;

  margin-right: 20px;
}
.main .aside .imagen img{
  height: 90px;
  width: 100%
}

/**Animaciones**/
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}



